// app/api/gas2/route.ts
import { NextRequest, NextResponse } from 'next/server';

function baseUrl() {
  const b =
    process.env.NEXT_PUBLIC_GAS_BASE ||
    process.env.GAS_BASE;
  if (!b) throw new Error('GAS base URL is not configured (set NEXT_PUBLIC_GAS_BASE or GAS_BASE).');
  return b.replace(/\/+$/, '');
}

function withTokenURL(url: string, token?: string) {
  if (!token) return url;
  const u = new URL(url);
  if (!u.searchParams.get('token')) u.searchParams.set('token', token);
  return u.toString();
}

async function passThrough(req: NextRequest) {
  const GAS = baseUrl();
  const token = process.env.GAS_TOKEN?.trim() || '';

  if (req.method === 'GET') {
    // forward all query params
    const out = withTokenURL(`${GAS}?${req.nextUrl.searchParams.toString()}`, token);
    const res = await fetch(out, { method: 'GET', cache: 'no-store' });
    const txt = await res.text();
    return new NextResponse(txt, {
      status: res.status,
      headers: { 'content-type': res.headers.get('content-type') || 'application/json' },
    });
  }

  if (req.method === 'POST') {
    // forward JSON body and ensure token is present
    let body: any = {};
    try { body = await req.json(); } catch {}
    if (token && !body.token) body.token = token;

    const res = await fetch(GAS, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      cache: 'no-store',
      body: JSON.stringify(body),
    });
    const txt = await res.text();
    return new NextResponse(txt, {
      status: res.status,
      headers: { 'content-type': res.headers.get('content-type') || 'application/json' },
    });
  }

  return NextResponse.json({ ok: false, error: 'Method not allowed' }, { status: 405 });
}

export async function GET(req: NextRequest)  { return passThrough(req); }
export async function POST(req: NextRequest) { return passThrough(req); }
