'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';
import type { Job } from '@/lib/api';
import { searchJobs, getJob, logCallSimple, markCalled } from '@/lib/api';

/* --- helpers for display-only price preview --- */
function normProc(s?: string) {
  const v = String(s || '').toLowerCase();
  if (v.includes('cape') && !v.includes('skull')) return 'Caped';
  if (v.includes('skull')) return 'Skull-Cap';
  if (v.includes('euro')) return 'European';
  if (v.includes('standard')) return 'Standard Processing';
  return '';
}
function suggestedProcessingPrice(proc?: string, beef?: boolean, webbs?: boolean) {
  const p = normProc(proc);
  const base =
    p === 'Caped' ? 150 :
    ['Standard Processing', 'Skull-Cap', 'European'].includes(p) ? 130 : 0;
  if (!base) return 0;
  return base + (beef ? 5 : 0) + (webbs ? 20 : 0);
}
const toInt = (val: any) => {
  const n = parseInt(String(val ?? '').replace(/[^0-9]/g, ''), 10);
  return Number.isFinite(n) && n > 0 ? n : 0;
};
function specialtyPriceFromRow(j: Partial<Job>) {
  if (!j.specialtyProducts) return 0;
  const ss  = toInt(j.summerSausageLbs);
  const ssc = toInt(j.summerSausageCheeseLbs);
  const jer = toInt(j.slicedJerkyLbs);
  return ss * 4.25 + ssc * 4.60 + jer * 15.0;
}

/* --- derive reason to contact --- */
type Reason = 'Meat Ready' | 'Cape Ready' | 'Webbs Ready' | '-';
function reasonForRow(j: Partial<Job>): Reason {
  const st = String(j.status || '').toLowerCase();
  const cp = String(j.capingStatus || '').toLowerCase();
  const wb = String(j.webbsStatus || '').toLowerCase();
  if (st.includes('finished')) return 'Meat Ready';
  if (cp.includes('caped'))    return 'Cape Ready';
  if (wb.includes('delivered'))return 'Webbs Ready';
  return '-';
}

/* --- pretty money --- */
function fmt(n: number | string | undefined | null) {
  const num = typeof n === 'number' ? n : Number(String(n ?? '').replace(/[^0-9.\-]/g, ''));
  if (!isFinite(num)) return '—';
  return num.toLocaleString('en-US', { style: 'currency', currency: 'USD' });
}

type Row = Partial<Job> & { tag: string };

export default function CallReportPage() {
  const [rows, setRows] = useState<Row[]>([]);
  const [notes, setNotes] = useState<Record<string, string>>({});
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState<string | null>(null);

  const load = async () => {
    setLoading(true);
    setErr(null);
    try {
      const res = await searchJobs('@report'); // Finished / Caped / Delivered
      setRows((res.rows || []).map((r: any) => ({ ...r })));
    } catch (e: any) {
      setErr(e?.message || 'Failed to load report');
      setRows([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => { load(); }, []);

  const paidText = (j: Row) =>
    (j.Paid || j.paid || (j.paidProcessing && j.paidSpecialty)) ? 'Yes' : 'No';

  const displayPrice = (j: Row) => {
    const got = (j as any).price ?? (j as any).Price ?? undefined;
    if (got != null && String(got).trim() !== '') return fmt(got);
    const proc = suggestedProcessingPrice(j.processType, !!j.beefFat, !!j.webbsOrder);
    const spec = specialtyPriceFromRow(j);
    return fmt(proc + spec);
  };

  const setNote = (tag: string, v: string) => setNotes((p) => ({ ...p, [tag]: v }));

  const refreshOne = async (tag: string) => {
    try {
      const r = await getJob(tag);
      if (r?.exists && r.job) {
        setRows((prev) => prev.map((x) => (x.tag === tag ? { ...x, ...r.job } : x)));
      } else {
        // On Mark Called, row may drop from the "@report" filter
        setRows((prev) => prev.filter((x) => x.tag !== tag));
      }
    } catch {}
  };

  const onMarkCalled = async (row: Row) => {
    const tag = row.tag!;
    const note = (notes[tag] || '').trim();
    try {
      await markCalled({ tag, scope: 'auto', notes: note });
      if (note) await logCallSimple({ tag, reason: reasonForRow(row), notes: note });
      await refreshOne(tag);
      setNote(tag, '');
    } catch (e: any) {
      alert(e?.message || 'Failed to mark as called.');
    }
  };

  /** Optimistic +1 Attempt -> persist via GAS (log-call) -> refresh row */
  const onPlusAttempt = async (row: Row) => {
    const tag = row.tag!;
    const note = (notes[tag] || '').trim();

    // optimistic bump
    setRows(prev =>
      prev.map(x =>
        x.tag === tag
          ? {
              ...x,
              callAttempts: Number(x.callAttempts || 0) + 1,
              callNotes: note
                ? (x.callNotes ? `${x.callNotes}\n${note}` : note)
                : x.callNotes,
            }
          : x
      )
    );

    try {
      const res = await logCallSimple({ tag, reason: reasonForRow(row), notes: note });
      if (!(res as any)?.ok) throw new Error('Server declined.');
      await refreshOne(tag); // pull authoritative count + timestamped notes
      setNote(tag, '');
    } catch (e: any) {
      // revert on failure
      setRows(prev =>
        prev.map(x =>
          x.tag === tag
            ? { ...x, callAttempts: Math.max(0, Number(x.callAttempts || 1) - 1) }
            : x
        )
      );
      alert(e?.message || 'Failed to add attempt.');
    }
  };

  return (
    <main>
      <div style={{ display: 'flex', alignItems: 'baseline', gap: 12 }}>
        <h1>Call Report</h1>
        <button className="btn" onClick={load} disabled={loading}>
          {loading ? 'Refreshing…' : 'Refresh'}
        </button>
        <span className="muted">Showing items that are ready to contact (Finished / Caped / Delivered)</span>
      </div>

      {err && (
        <div className="card" style={{ borderColor: '#ef4444', marginTop: 12 }}>
          Error: {err}
        </div>
      )}

      <div className="card" style={{ padding: 0, marginTop: 12, overflow: 'hidden' }}>
        <table className="table" style={{ width: '100%', margin: 0, tableLayout: 'fixed' }}>
          <thead>
            <tr>
              <th style={{ width: 110 }}>Tag</th>
              <th>Name</th>
              <th style={{ width: 160 }}>Phone</th>
              <th style={{ width: 140 }}>Reason</th>
              <th style={{ width: 120 }}>Price</th>
              <th style={{ width: 80 }}>Paid</th>
              <th style={{ width: 120 }}># Contacts</th>
              <th>Contact Notes</th>
              <th style={{ width: 240 }}>Actions</th>
            </tr>
          </thead>
          <tbody>
            {(!rows || rows.length === 0) && (
              <tr>
                <td colSpan={9} style={{ padding: 14 }}>Nothing to call right now.</td>
              </tr>
            )}
            {rows.map((r) => {
              const reason = reasonForRow(r);
              return (
                <tr key={r.tag} style={{ background: 'transparent' }}>
                  <td>
                    <Link href={`/intake?tag=${encodeURIComponent(r.tag!)}`} title="Open form">{r.tag}</Link>
                  </td>
                  <td>{r.customer || '—'}</td>
                  <td>{r.phone || '—'}</td>
                  <td>{reason}</td>
                  <td>{displayPrice(r)}</td>
                  <td>{paidText(r)}</td>
                  <td>{Number(r.callAttempts || 0)}</td>
                  <td>
                    <div style={{ display: 'grid', gap: 8 }}>
                      {/* new note (bigger) */}
                      <textarea
                        value={notes[r.tag!] || ''}
                        onChange={(e) => setNote(r.tag!, e.target.value)}
                        placeholder="Add a note (e.g., Will pickup Friday 4–6pm)…"
                        rows={3}
                        style={{
                          width: '100%',
                          resize: 'vertical',
                          minHeight: 60,
                          background: '#fff',
                          color: '#0b0f12',
                          border: '1px solid #cbd5e1',
                          borderRadius: 10,
                          padding: '8px 10px',
                        }}
                      />
                      {/* prior notes */}
                      {r.callNotes ? (
                        <div
                          className="muted"
                          style={{
                            whiteSpace: 'pre-wrap',
                            wordBreak: 'break-word',
                            border: '1px dashed var(--border, #e5e7eb)',
                            borderRadius: 10,
                            padding: 8,
                            maxHeight: 160,
                            overflow: 'auto',
                            background: 'var(--bg-elev, #f8fafc)',
                          }}
                        >
                          {r.callNotes}
                        </div>
                      ) : (
                        <div className="muted">No prior notes</div>
                      )}
                    </div>
                  </td>
                  <td>
                    <div style={{ display: 'flex', gap: 8, flexWrap: 'wrap' }}>
                      <button
                        type="button"
                        className="btn"
                        onClick={() => onMarkCalled(r)}
                        title="Flip appropriate status (Meat/Cape/Webbs) to Called and log note"
                      >
                        Mark Called
                      </button>
                      <button
                        type="button"
                        className="btn secondary"
                        onClick={() => onPlusAttempt(r)}
                        title="Just add an attempt (and note), no status change"
                      >
                        +1 Attempt
                      </button>
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>

      <style jsx>{`
        /* keep rows readable on hover */
        .table tr:hover td { background: var(--bg-elev-2, rgba(0,0,0,.06)); }
      `}</style>
    </main>
  );
}



